sition: width 500ms ease 0s;\n    }\n    .slideIn {\n      transition: width 500ms ease 0s;\n      width: 250;\n    }\n    .exhibition-info {\n      flex: 1;\n      height: 100%;\n      overflow: hidden;\n      padding: 0 40px 20px 20px;\n      background: #ECF0F5;\n      @include css3(display, flex);\n      flex-direction: column;\n      .main-header-wrap{\n        overflow: hidden;\n      }      \n      .main-header {          \n        position: relative;left: 16px;\n        background: #fff;\n//      min-height: 50px;\n        padding: 10px 56px 10px 36px;\n        @include box-shadow(1px, 2px, 5px, rgba(0, 0, 0, .2));\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        flex-wrap: wrap;       \n        .main-header-left {\n//        margin-top: 15px;\n//        @include float();\n          >i {\n            display: inline-block;\n            width: 20px;\n            height: 20px;\n            background: url('../../assets/images/ic_hm_location.png') no-repeat;\n            margin-right: 5px;\n          }\n          >.appPresent {\n            display: inline-block;\n            vertical-align: top;\n            margin-top: 2px;\n            font-size: $systemFontSize;\n          }\n          .main-crumbs {\n            display: inline-block;\n            vertical-align: top;\n            margin-top: 4px;\n            >span {\n              font-size:$systemFontSize;\n            }\n          }\n        }\n        .main-header-right {\n//        @include float(right);\n          .searchKey {\n//          margin-top: 10px;\n            >i {\n              font-size: 18px;\n            }\n            >input {\n              height: 30px;\n              font-size: $systemFontSize;\n              border: 0;\n              background: #ECF0F5;\n              border-radius: 6px;\n            }\n          }\n        }\n      }\n      .main-content-wrap{\n          overflow: hidden;\n          height: 100%;\n          flex: 1;\n          margin-top: 30px;\n      }\n      .main-content {\n        overflow-x: hidden;\n        overflow-y: scroll;\n        position: relative;left: 16px;\n        height: 100%;\n      }\n    }\n  }\n  .section-shade {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    background: #000;\n    opacity: 0.7;\n    z-index: 1000 !important;\n  }\n  .section-window {\n    margin: auto;\n    position: fixed;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    width: 40%;\n    min-width: 460px;\n    height: 255px;\n    border: 1px solid $essentialColour;\n    background: #fff;\n    z-index: 9999 !important;\n    @include border-radius(6px);\n    .add-section-top {\n      background: $essentialColour;\n      padding: 10px;\n      color: #fff;\n      >i {\n        @include float(right);\n      }\n    }\n    .add-section-info {\n      padding: 20px 20px 0;\n    }\n    .add-section-btn {\n      padding-left: 20px;\n    }\n    .save-btn{\n        margin-top: 40px;\n    }\n  }\n  .staff-window {\n    @extend .section-window;\n  }\n  .fade-enter-active,\n  .fade-leave-active {\n    transition: opacity .2s\n  }\n  .fade-enter,\n  .fade-leave-to {\n    opacity: 0\n  }\n  @media (min-width:1400px){\n    .navigation{\n      display: flex;\n      flex-direction: column;\n      width: 55%;\n      min-width: 1000px;\n      min-height: 700px;\n      position: fixed;\n      z-index: 9999 !important;\n      top: 50%;\n      left: 50%;\n      margin-left: -28%;\n      margin-top: -350px;\n      border: 1px solid $essentialColour;\n      border-radius: 3px !important;\n      background: #fff;\n      .navigation-top{\n          display: flex;\n          justify-content: space-around;\n          height:620px;\n          background: #fff;\n          border-bottom: 1px solid $essentialColour;\n          >span{\n              flex:1;\n              padding:0 20px;\n          }\n          >span:first-child{\n            border-right: 1px solid $essentialColour;\n              img{\n                  display: block;\n                  border:0;\n                  width:100%;\n              }\n          }\n          >span:last-child{\n              img{\n                  display: block;\n                  border:0;\n                  width:100%;\n              }\n          }\n      }\n      >.navigation-buttom{\n          flex:1;\n          text-align: center;\n          background: #fff;\n          margin:10px 0;\n          >p{\n              >span{\n                  display: inline-block;\n                  font-size: 24px;\n                  vertical-align: top;\n                  margin:10px 10px 0 0;\n                  color:#000;\n              }\n              >i{\n                  display: inline-block;\n                  width:78px;\n                  height:48px;\n                  background:url('../../assets/images/ic_arrow.png') no-repeat;\n              }\n          }\n      }\n    }\n  }\n  @media (max-width:1400px){\n    .navigation{\n      display: flex;\n      flex-direction: column;\n      width: 70%;\n      min-width: 820px;\n      min-height:550px;\n      position: fixed;\n      z-index: 9999 !important;\n      top: 50%;\n      left: 50%;\n      margin-left: -35%;\n      margin-top: -275px;\n      border: 1px solid $essentialColour;\n      border-radius: 3px !important;\n      background: #fff;\n      .navigation-top{\n          display: flex;\n          justify-content: space-around;\n          height:480px;\n          background: #fff;\n          border-bottom: 1px solid $essentialColour;\n          >span{\n              flex:1;\n              padding:0 20px;\n          }\n          >span:first-child{\n            border-right: 1px solid $essentialColour;\n              img{\n                  display: block;\n                  border:0;\n                  height:470px;\n                  width:100%;\n              }\n          }\n          >span:last-child{\n              img{\n                  display: block;\n                  border:0;\n                  height:470px;\n                  width:100%;\n              }\n          }\n      }\n      >.navigation-buttom{\n          flex:1;\n          text-align: center;\n          background: #fff;\n          margin:10px 0;\n          >p{\n              >span{\n                  display: inline-block;\n                  font-size: 20px;\n                  vertical-align: top;\n                  margin:10px 10px 0 0;\n                  color:#000;\n              }\n              >i{\n                  display: inline-block;\n                  width:78px;\n                  height:48px;\n                  background:url('../../assets/images/ic_arrow.png') no-repeat;\n              }\n          }\n      }\n    }\n  }\n}\n","// =============================================================================\n// String Replace\n// =============================================================================\n$systemFontSize : 14px;\n$essentialColour: #2dbad2; // 主色\n$baseColor : #26587D;\n$addBtn : #60CEB8; // 按钮颜色\n$inputColor : #F7F6F9;\n$inputLogo : #E7E7E7;\n$firstList : #F3F7F7; // 表格第一列颜色\n$editor : #23a9db;  // 编辑按钮\n$delete : #ed6965; // 删除按钮\n\n@function str-replace($string, $search, $replace: \"\") {\n\t$index: str-index($string, $search);\n\n\t@if $index {\n\t\t@return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);\n\t}\n\n\t@return $string;\n}\n\n// =============================================================================\n// Font Face\n// =============================================================================\n\n@mixin font-face($name, $path, $weight: null, $style: null, $exts: eot woff2 woff ttf svg) {\n\t$src: null;\n\n\t$extmods: (\n\t\teot: \"?\",\n\t\tsvg: \"#\" + str-replace($name, \" \", \"_\")\n\t);\n\n\t$formats: (\n\t\totf: \"opentype\",\n\t\tttf: \"truetype\"\n\t);\n\n\t@each $ext in $exts {\n\t\t$extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);\n\t\t$format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);\n\t\t$src: append($src, url(quote($path + \".\" + $extmod)) format(quote($format)), comma);\n\t}\n\n\t@font-face {\n\t\tfont-family: quote($name);\n\t\tfont-style: $style;\n\t\tfont-weight: $weight;\n\t\tsrc: $src;\n\t}\n}\n\n\n@mixin box-shadow($top, $left, $blur, $color, $inset: false) {\n  @if $inset {\n    -webkit-box-shadow:inset $top $left $blur $color !important;\n    -moz-box-shadow:inset $top $left $blur $color !important;\n    box-shadow:inset $top $left $blur $color !important;\n  } @else {\n    -webkit-box-shadow: $top $left $blur $color !important;\n    -moz-box-shadow: $top $left $blur $color !important;\n    box-shadow: $top $left $blur $color !important;\n  }\n}\n\n@mixin text-field {\n display: inline-block;\n  outline: none;\n  text-decoration: none;\n  font: 14px/100% Arial, Helvetica, sans-serif;\n  padding: .5em;\n  text-shadow: 0 1px 1px rgba(0,0,0,.3);\n  @include rounded();\n  @include box-shadow(0, 1px, 2px, rgba(0, 0, 0, 0.2));\n}\n\n@mixin button($color: $red, $text_color: $white) {\n  display: inline-block;\n  outline: none;\n  cursor: pointer;\n  text-align: center;\n  text-decoration: none;\n  font: 14px/100% Arial, Helvetica, sans-serif;\n  padding: .5em 2em .55em;\n  text-shadow: 0 1px 1px rgba(0,0,0,.3);\n  @include rounded();\n  @include box-shadow(0, 1px, 2px, rgba(0, 0, 0, 0.2));\n\n  color: $text_color !important;\n  font-weight: bold;\n  border: solid 1px darken($color, 18%);\n  background: $color;\n  @include gradient(saturate($color, 15%), darken($color, 15%));\n\n  &:hover {\n    text-decoration: none;\n    background: saturate($color, 10%);\n    @include gradient(saturate($color, 5%), darken($color, 5%));\n  }\n\n  &:active {\n    position: relative;\n    top: 1px;\n    color: saturate($color, 15%);\n    @include gradient(saturate($color, 15%), lighten($color, 15%));\n  }\n}\n\n@mixin rounded($radius: 0.5em) {\n  -webkit-border-radius: $radius;\n  -moz-border-radius: $radius;\n  border-radius: $radius;\n}\n\n@mixin gradient($from, $to) {\n  background: -webkit-gradient(linear, left top, left bottom, from($from), to($to));\n  background: -moz-linear-gradient(top,  $from, $to);\n  filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#{$from}', endColorstr='#{$to}');\n}\n\n@mixin border-radius($radius) {\n  -webkit-border-radius: $radius !important;\n     -moz-border-radius: $radius !important;\n      -ms-border-radius: $radius !important;\n          border-radius: $radius !important;\n}\n\n@mixin css3($property, $value) {  \n    @each $prefix in -webkit-,  \n    -moz-,  \n    -ms-,  \n    -o-,  \n    '' {  \n        #{$prefix}#{$property}: $value !important;\n    }  \n}\n\n@mixin float($float:left){\n  float:unquote($float);\n}\n"],"sourceRoot":""}]);

